import { createReducer } from '@reduxjs/toolkit'
import { forEach } from 'lodash'

import {
    showDrink, setDrinksData, resetDrinks, setDrinksPrefsView
} from './drinks-actions'

import {
    ingToggleWant, ingToggleNotWant
} from '../ings/ings-actions'

import { selectDrinks } from './drinks'

export const DRINKS_PREFS_VIEW_INGS = 'Ings'
export const DRINKS_PREFS_VIEW_DRINKS_LIST = 'DrinkList'

const initialState = {
    origDrinks: [],
    drinks: [],
    drink: {},
    origIngs: {},
    ings: {},
    drinksPrefs: { view: DRINKS_PREFS_VIEW_INGS }
}

const resetToInitialState = state => {
    forEach(initialState, (v, k) => state[k] = v);
}

// https://redux-toolkit.js.org/api/createreducer/
const drinkReducer = createReducer(initialState, {

    [setDrinksData]: (state, action) => {
        resetToInitialState(state)
        state.origDrinks = action.payload.drinks
        state.origIngs = action.payload.ings
        state.ings = action.payload.ings

        state.drinks = selectDrinks(state.origDrinks, state.ings)
    },

    [showDrink]: (state, action) => {
        state.drink = action.payload
    },

    [resetDrinks]: (state, acion) => {
        state.drinks = selectDrinks(state.origDrinks, state.ings)
        state.drink = {}
        state.ings = state.origIngs
        state.drinksPrefs.view = DRINKS_PREFS_VIEW_INGS
    },

    [ingToggleWant]: (state, action) => {
        const ingId = action.payload.id
        const ing = state.ings[ingId]
        const wanted = ing.wanted

        state.ings[ingId].wanted = !wanted
        state.ings[ingId].notWanted = false

        state.drinks = selectDrinks(state.origDrinks, state.ings)
    },

    [ingToggleNotWant]: (state, action) => {
        const ingId = action.payload.id
        const ing = state.ings[ingId]
        const notWanted = ing.notWanted

        state.ings[ingId].wanted = false
        state.ings[ingId].notWanted = !notWanted

        state.drinks = selectDrinks(state.origDrinks, state.ings)
    },

    [setDrinksPrefsView]: (state, action) => {
        state.drinksPrefs.view = action.payload
    }
})

export default drinkReducer
